<template>
	<div id="container">
		<div class="top">
			<el-input placeholder="请输入内容" v-model="souObj.select_title" class="input-with-select">
				<el-select v-model="souObj.select" slot="prepend" placeholder="请选择">
					<!-- <el-option label="用户名" value="user_id"></el-option> -->
					<el-option label="区号" value="area_code"></el-option>
					<el-option label="用户名" value="name"></el-option>
					<el-option label="全部" value="all"></el-option>
				</el-select>
				<el-button slot="append" icon="el-icon-search"></el-button>
			</el-input>
			<el-button type="primary" @click="addObj.flag  = true">创建用户</el-button>
		</div>
		
		<el-table 
			stripe
			:data="tableData" 
			style="width: 100%"
			max-height='730px'
			v-loading="loading"
			>
			<el-table-column prop="id" label="ID"> </el-table-column>
			<el-table-column prop="name" label="用户名"> </el-table-column>
			<el-table-column prop="phone" label="用户电话"> </el-table-column>
			<el-table-column prop="md[0].name" label="店主名"> </el-table-column>
			<el-table-column prop="md[0].phone" label="店主电话"> </el-table-column>
			<el-table-column prop="q_id" label="权限"> </el-table-column>
			<el-table-column prop="state" label="状态"> </el-table-column>
			<el-table-column fixed="right" label="操作">
			  <template slot-scope="scope">
				<el-button size="mini" @click="handleEdit(scope.$index, scope.row);">编辑</el-button>
				<el-button size="mini" @click.native.prevent="handleDelete(scope.$index, scope.row)">移除</el-button>
			  </template>
			</el-table-column>
		</el-table>

		<!-- 添加 -->
		<el-dialog title="创建用户" :visible.sync="addObj.flag " width="50%" center>
			<el-form label-position="left" label-width="100px">
				<el-form-item label="账号名:">
					<el-input v-model="addObj.ename" type="text" size="small"></el-input>
				</el-form-item>
				<el-form-item label="账号密码:">
					<el-input v-model="addObj.password" type="password" size="small" :show-password="true" ></el-input>
				</el-form-item>
				<el-form-item label="账号手机号:">
					<el-input v-model="addObj.phone" type="number" size="small"></el-input>
				</el-form-item>
				<el-form-item label="账号权限:">
					<el-checkbox-group v-model="addObj.checkList">
						<el-checkbox label="门店主"></el-checkbox>
						<el-checkbox label="管理员"></el-checkbox>
						<el-checkbox label="财务"></el-checkbox>
					</el-checkbox-group>
				</el-form-item>
				<el-divider></el-divider>
				<el-form-item label="店主名:">
					<el-input v-model="addObj.dname" type="text" size="small"></el-input>
				</el-form-item>
				<el-form-item label="店主手机号:">
					<el-input v-model="addObj.dphone" type="number" size="small"></el-input>
				</el-form-item>
				<el-form-item label="身份证号:">
					<el-input v-model="addObj.s_id" type="number" size="small"></el-input>
				</el-form-item>
				<el-form-item label="性别:">
					<el-radio-group v-model="addObj.dsex">
						<el-radio label="男"></el-radio>
						<el-radio label="女"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="地址:">
					 <el-cascader
						placeholder="找不到？试试搜索:"
						:options="space"
						filterable
						v-model="addObj.dizhi"></el-cascader>
				</el-form-item>
				<el-form-item label="详细地址:">
					<el-input
					  type="textarea"
					  :rows="2"
					  placeholder="请输入内容"
					  v-model="addObj.address">
					</el-input>
				</el-form-item>	
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="addclear()">取 消</el-button>
				<el-button type="primary" @click="addTrue()">确 定</el-button>
			</div>
		</el-dialog>

		<!-- 编辑 -->
		<el-dialog title="编辑信息" :visible.sync="changeObj.flag" width="50%" center>
		  <el-form label-position="left" label-width="100px">
		  	<el-form-item label="账号权限:">
		  		<el-checkbox-group v-model="changeObj.checkList">
		  			<el-checkbox label="门店主"></el-checkbox>
		  			<el-checkbox label="管理员"></el-checkbox>
		  			<el-checkbox label="财务"></el-checkbox>
					<el-checkbox label="超级管理员"></el-checkbox>
		  		</el-checkbox-group>
		  	</el-form-item>
			<el-form-item label="账号状态:">
				<el-radio-group v-model="changeObj.state">
					<el-radio label="正常"></el-radio>
					<el-radio label="封停"></el-radio>
				</el-radio-group>
			</el-form-item>
		  	<el-divider></el-divider>
		  	<el-form-item label="店主手机号:">
		  		<el-input v-model="changeObj.phone" type="number" size="small"></el-input>
		  	</el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
			<el-button @click="changeObj.flag=false">取 消</el-button>
			<el-button type="primary" @click="changTrue">确 定</el-button>
		  </div>
		</el-dialog>

	<el-pagination
		v-if="tableData"
		background
		layout="sizes, prev, pager, next, jumper"
		:total="fenye.total"
		:page-sizes="[10,20,30,50]"
		@current-change="yebian"
		@size-change='tiaobian'
		:page-size="fenye.page_size"
		:current-page="fenye.current_page">
	</el-pagination>
	</div>
</template>

<script>

 export default {
	 //将每组需要操作的数据，分成每个对象，在方法里进行再次的处理
	data() {
		return {
			//公用部分
			tableData: [{name:'sad'}],//列表的数据
			space:'',//地址的数据（从vuex里获得）
			loading:true,//加载
			souObj:{//搜索用
				select_title:'',
				select:'all'
			},
			addObj:{//添加用
				ename:'',
				password:'',
				phone:'',
				checkList:['门店主'],
				dname:'',
				dphone:'',
				s_id:'',
				dsex:'男',
				dizhi:'',
				address:'',
				flag:false
			},
			fenye:{//分页用
				total:1,//总条数
				page_size:10,//单页条数
				current_page:1,//页码
			},
			changeObj:{//编辑用
				flag:false,
				dname:'dsad',
				user_id:'',
				phone:'',
				state:'',
				q_id:'',
				checkList:[],
				id:''
			}
		}
	},
	methods:{
		//编辑确定按钮
		changTrue(){
			this.loading = true
			let obj = {
				token:this.$store.state.userInfo.token,
				name:this.$store.state.userInfo.name,
				phone:this.changeObj.phone,
				id:this.changeObj.id,
				q_id:''
			}
			this.changeObj.checkList.forEach(function(val,ind){
				if(val == '门店主'){
					obj.q_id += '4'
				}else if(val == '财务'){
					obj.q_id += '3'
				}else if(val == '管理员'){
					obj.q_id += '2'
				}else{
					obj.q_id += '1'
				}
			})
			if(this.changeObj.state == '正常'){
				obj.state = 0
			}else{
				obj.state = 1
			}
			this.$axios.post("/userEdit",obj).then(() => {
				this.userList(this.fenye.current_page,this.fenye.page_size)
				this.changeObj.flag = false
				this.$message({
					type: 'success',
					message: '修改成功!'
				});
			}).catch(() => {
				this.changeObj.flag = false
				this.$message({
					type: 'info',
					message: '修改失败'
				});
			});
		},
		
		
		//编辑按钮
		handleEdit(index, row) {
			this.changeObj.flag = true
			this.changeObj.phone = this.tableData[index].phone
			this.changeObj.state = this.tableData[index].state
			this.changeObj.checkList = this.tableData[index].checkList
			this.changeObj.id = this.tableData[index].id
		},
		//删除按钮
		handleDelete(index, row) {
			this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				this.tableData.splice(index,1)
				this.$message({
					type: 'success',
					message: '删除成功!'
				});
			}).catch(() => {
				this.$message({
					type: 'info',
					message: '已取消删除'
				});
			});
		},
		//分页
		yebian(currentPage){//页面的页码发生变化的时候
			this.loading = true
			this.fenye.current_page = currentPage
			this.userList(currentPage,this.fenye.page_size)
		},
		tiaobian(ind){//页面的条数发生变化的时候
			this.loading = true
			this.fenye.page_size = ind
			this.userList(this.fenye.current_page,ind)
		},
		//用户列表
		userList(ind,num){
			let that = this
			let name = this.$store.state.userInfo.name
			let token = this.$store.state.userInfo.token
			let obj = {
				'name':this.$store.state.userInfo.name,
				'token':this.$store.state.userInfo.token,
				'page':ind,
				'pageSize':num
			}
			this.$axios.post("/user",obj).then(function(res){
				that.fenye.total = res.data.pageCount * res.data.pageSize
				that.tableData = res.data.data
				that.tableData.forEach(function(val,ind){
					if(val.state == 0){
						val.state = '正常'
					}else{
						val.state = '封停'
					}
					
					let arr = String(val.q_id).split("")
					val.checkList = []
					for(let i = 0;i < arr.length;i++){
						if(arr[i] == '4'){
							val.checkList.push('门店主')
						}else if(arr[i] == '2'){
							val.checkList.push('管理员')
						}else if(arr[i] == '3'){
							val.checkList.push('财务')
						}else{
							val.checkList.push('超级管理员')
						}
					}
					val.q_id = val.checkList.join(',')
				})
				console.log(that.tableData)
				that.loading = false
			})
		},
		//创建用户的确定按钮
		addTrue(){
			this.loading = true
			//下面是处理数据，传给后台
			let that = this
			let obj = this.addObj
			let dizhi = this.addObj.dizhi
			let q_id = ''
			//权限
			this.addObj.checkList.forEach(function(val,ind){
				if(val == '门店主'){
					q_id += '4'
				}else if(val == '财务'){
					q_id += '3'
				}else if(val == '管理员'){
					q_id += '2'
				}
			})
			//账号的名字
			obj.name = this.$store.state.userInfo.name
			//账号的token（目的是为了验证，到底是那个人提交的信息）
			obj.token = this.$store.state.userInfo.token
			//账号的权限
			obj.q_id = q_id
			//性别判断
			if(obj.dsex == '男'){
				obj.dsex = 0
			}else if(obj.dsex == '女'){
				obj.dsex = 1
			}else{
				obj.dsex = 2
			}
			//地址的拆分（省，市）
			obj.province = dizhi[0]
			obj.city = dizhi[1]
			//提交申请
			this.$axios.post("/userAdd",obj).then(function(res){
				//创建失败（用户名重复）
				if(res.data.code == '1'){
					that.$message({
						type: 'info',
						message: res.data.message,
					}); 
				}else{
				//创建用户名成功
					that.userList(that.fenye.current_page,that.fenye.page_size)
					that.$message({
						type: 'success',
						message: res.data.message
					});
				}
				that.addclear()
			})
		},
		//在创建用户完成或取消，消除内容
		addclear(){
			this.addObj.ename = ''
			this.addObj.password = ''
			this.addObj.phone = ''
			this.addObj.checkList = ['门店主']
			this.addObj.dname = ''
			this.addObj.dphone = ''
			this.addObj.s_id=''
			this.addObj.dsex = '男'
			this.addObj.dizhi = ''
			this.addObj.address=''
			this.addObj.flag = false
		}
	},
	//挂载完成执行的生命钩子函数
	created () {
		//或得店主列表信息
		this.userList(this.fenye.current_page,this.fenye.page_size)
		//获得三级菜单，处理数据
		this.space = this.$store.state.space
	}
}
</script>



<style lang="less">
#container{
  padding-right: 10px;
}
.top{
  width:100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.el-input-group{
	width: 50%;
}
.el-select .el-input {
	width: 130px;
}
.input-with-select .el-input-group__prepend {
	background-color: #fff;
}
</style>

																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																																										  